//---------------------------------------------------------------------------------------------------------------------
export function* filter(iterator, func) {
    let i = 0;
    for (const el of iterator) {
        if (func(el, i++))
            yield el;
    }
}
//---------------------------------------------------------------------------------------------------------------------
